<template>
  <el-row>
    <el-col :span="18" :offset="3">
      <el-card shadow="hover">
        <div slot="header" class="clearfix">
          <span>客户投资</span>
          <router-link :to="'/customer/index'">
            <el-button type="info" size="small" style="float:right; padding: 8px 10px" icon="el-icon-back">返回客户列表</el-button>
          </router-link>
        </div>
        <el-form :model="investForm" :rules="rules" ref="investForm" label-width="150px">
          <el-form-item label="投资人" prop="customerId">
            <el-col :span="20">
              <el-select style="width:100%" v-model="investForm.customerId" filterable placeholder="请选择">
                <el-option
                  v-for="item in allCustomerList"
                  :key="item.customerId"
                  :label="item.customerName"
                  :value="item.customerId">
                  <span style="float: left">{{ item.customerName }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.idCard }}</span>
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="投资人身份证件号" prop="customerIdCard">
            <el-col :span="20">
              <el-input v-model="investForm.customerIdCard" :readonly=true></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="投资项目" prop="projectId">
            <el-col :span="20">
              <el-select style="width:100%" v-model="investForm.projectId" filterable placeholder="请选择">
                <el-option
                  v-for="item in allProjectList"
                  :key="item.projectId"
                  :disabled="item.disabled"
                  :label="item.projectName"
                  :value="item.projectId">
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="投资金额" prop="investMoney">
            <el-col :span="20">
              <el-input v-model.number="investForm.investMoney" clearable></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="打款时间" prop="investTime">
            <el-col :span="20">
              <div class="block">
                <el-date-picker style="width:100%"
                  v-model="investForm.investTime"
                  type="date"
                  placeholder="选择日期时间"
                  align="right"
                  value-format="yyyy-MM-dd" format="yyyy-MM-dd">
                </el-date-picker>
              </div>
            </el-col>
          </el-form-item>
          <el-form-item label="利率">
            <el-tag effect="dark" v-model="investForm.rate" v-text="investForm.rate"></el-tag>
          </el-form-item>
          <br>
          <el-form-item>
            <el-row>
              <el-button type="warning" icon="el-icon-refresh" @click="resetForm('investForm')">重置</el-button>
              <el-button type="primary" icon="el-icon-success" :loading="btnLoading" @click="submitForm('addProjectCustomer')">保存</el-button>
            </el-row>
          </el-form-item>
        </el-form>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
  import { getAllCustomers } from '@/api/customer'
  import { addProjectCustomer, getCustomerInvestProject } from '@/api/projectCustomer'
  import { getAllProjects, getRateRange } from '@/api/project'

  const defaultForm = {
    id:'',
    customerId:'',
    projectId:'',
    phase:-1,
    customerIdCard:'',
    investMoney:'',
    investTime:'',
    rate:'',
  };

  export default {
    name: 'ProjectCustomerDetail',
    components: {},
    props: {
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    watch:{
      'investForm.customerId':{
        handler(newVal, oldVal) {
          if (newVal) {
            this.getCustomerInvestProjectList(newVal);
            let tempArray = this.allCustomerList;
            let selectedCustomer = tempArray.find(function (x) {
              return x.customerId === newVal
            });
            this.investForm.customerIdCard = selectedCustomer.idCard;
          }
        },
        deep: true
      },
      'investForm.projectId':{
        handler(newVal, oldVal) {
          this.getRateRange();
        },
        deep: true
      },
      'investForm.investMoney':{
        handler(newVal, oldVal) {
          this.getRateRange();
        },
        deep: true
      },
    },
    data() {
      return {
        allProjectList: [],
        allCustomerList: [],
        customerInvestProjectList: [],
        investForm: Object.assign({}, defaultForm),
        btnLoading: false,
        rules: {
          customerId: [
            { required: true, message: '请选择投资人', trigger: 'blur' }
          ],
          customerIdCard: [
            { required: true, message: '若投资人身份证号为空，请联系管理员', trigger: 'blur' }
          ],
          projectId: [
            { required: true, message: '请选择投资项目', trigger: 'blur' }
          ],
          investMoney: [
            { required: true, message: '请输入投资金额', trigger: 'blur' },
            { type: 'number', message: '投资金额必须为数字值'}
          ],
          investTime: [
            { required: true, message: '请选择投资时间', trigger: 'blur' }
          ]
        },
      }
    },
    created() {
      if (this.hasPermission('project:list')) {
        if (this.isEdit) {
          // TODO
        } else {
          this.investForm = Object.assign({}, defaultForm);
          this.getAllProjectsList();
          this.getAllCustomersList();
        }
      }
    },
    methods: {
      getAllProjectsList() {
        getAllProjects().then(response => {
          let temp = response.data;
          temp.forEach(project => project.disabled = false);
          this.allProjectList = temp;
        })
      },
      getAllCustomersList(){
        getAllCustomers().then(response => {
          this.allCustomerList = response.data.list
        })
      },
      getCustomerInvestProjectList(){
        this.allProjectList.forEach(project => project.disabled = false);
          getCustomerInvestProject(this.investForm.customerId).then(response =>{
            this.customerInvestProjectList = response.data;
            for (let i = 0; i < this.allProjectList.length; i++) {
              this.customerInvestProjectList.forEach(investedProject =>{
                if (investedProject.projectId === this.allProjectList[i].projectId) {
                  this.allProjectList[i].disabled = true;
                }
              })
            }
          })
      },
      addProjectCustomer(form){
        this.btnLoading = true;
        addProjectCustomer(form).then((response) => {
          if(response.returnCode === 200) {
            this.$message.success('客户投资信息已录入');
            this.btnLoading = false;
          }else{
            this.$message.error('客户投资信息录入失败');
            this.btnLoading = false;
          }
        })
      },
      submitForm() {
        this.$refs.investForm.validate(valid => {
          if (valid) {
            if(this.isEdit) {
              // TODO
            }else{
              this.addProjectCustomer(this.investForm);
            }
          } else {
            console.error('error submit!!');
            return false;
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.investForm.rate = '';
      },

      getRateRange(){
        let projectId = this.investForm.projectId;
        let investMoney = this.investForm.investMoney;
        if(projectId != '' && investMoney != ''){
          if (Number.isInteger(investMoney)) {
            getRateRange(projectId,investMoney).then(response =>{
              if (!response.data) {
                this.investForm.rate = '投资金额不在该项目的利息区间内'
              }else{
                this.investForm.rate = response.data;
              }
            })
          }else{
            this.$message.error("投资金额必须为数字");
          }
        }
      }
    }
  };
</script>

<style>

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

</style>
